/*PRESETS*/

*,
*:after,
*:before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  vertical-align: middle;
  margin: 0;
}

a img {
  border: none;
  margin: 0;
}

.container {
  display: block;
  width: 100%;
}

.content {
  width: 90%;
  margin: 0 5%;
  padding: 40px 0;
  max-width: 120em;
}

.content.g {
  padding: 60px 0;
}

@media (max-width: 60em) {
  .content {
    width: 94%;
    margin: 0 3%;
  }
}

@media (min-width: 100em) {
  .content {
    width: 84%;
    margin: 0 8%;
  }
}

body {
  background: #fbfbfb;
}

p {
  margin-bottom: 20px;
  line-height: 1.5;
}

p:last-of-type {
  margin-bottom: 0;
}

p,
a,
li,
h1,
h2,
h3,
h4,
h5,
h6,
blockquote,
cite,
span {
  font-size: 1em;
  font-weight: normal;
}

mark {
  padding: 5px 10px;
  background-color: #eee;
}

small {
  font-size: 0.75em;
  color: #555;
}

a {
  text-decoration: none;
  cursor: pointer;
}

ul {
  list-style: none;
}

embed,
video,
iframe,
iframe[style] {
  max-width: 100%;
  height: auto;
}

.embed-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%;
}

.embed-container iframe,
.embed-container object,
.embed-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

fieldset {
  border: 0;
}

input,
select,
textarea {
  padding: 10px;
  width: 100%;
}

input.btn {
  width: auto;
  cursor: pointer;
}

iframe[name="google_conversion_frame"] {
